import React, { memo, useMemo, useState, useRef, useCallback } from 'react';
import { I_Hotspot, I_Post, I_Scene } from 'containers/Home/types';
import dynamic from 'next/dynamic';
import { Popover } from 'antd';
import {
  COLOR_HOTSPOT_BG_DEFAULT,
  COLOR_HOTSPOT_ICON_DEFAULT,
} from '@containers/Home/constants';
import { HotspotWrapper } from './styled';

interface Props {
  isHidden?: boolean;
  isHideTitleOnTopGlobal?: boolean;
  isHideTitleGlobal?: boolean;
  isHidePreviewGlobal?: boolean;
  hotspot: I_Hotspot;
  listScene: I_Scene[];
  listPost: I_Post[];
  onClick?: () => void;
  bgColorDefault?: string;
  iconColorDefault?: string;
  onChangePopover: (visible: boolean) => void;
}

const DefaultIcon = dynamic(() => import('./DefaultIcon'));
const ShapeIcon = dynamic(() => import('./ShapeIcon'));
const ImageIcon = dynamic(() => import('./ImageIcon'));
const LottieIcon = dynamic(() => import('./LottieIcon'));
const MarkerIcon = dynamic(() => import('./MarkerIcon'));
const CalloutIcon = dynamic(() => import('./CalloutIcon'));
const Preview = dynamic(() => import('./Preview'), { ssr: false });

const Hotspot = ({
  hotspot,
  listScene,
  listPost,
  onChangePopover,
  isHidden = false,
  isHideTitleGlobal = false,
  isHidePreviewGlobal = false,
  isHideTitleOnTopGlobal = false,
  onClick = () => {},
  bgColorDefault = COLOR_HOTSPOT_BG_DEFAULT,
  iconColorDefault = COLOR_HOTSPOT_ICON_DEFAULT,
}: Props) => {
  const flagCheckVisibleRef = useRef(false);
  const [visiblePopover, setVisiblePopover] = useState(false);

  const isStyleShape = useMemo(
    () =>
      hotspot?.config?.style?.type === 'icon' &&
      (hotspot?.config?.style?.icon?.icon ||
        hotspot?.config?.style?.icon?.shape),
    [hotspot],
  );
  const isStyleImage = useMemo(
    () =>
      hotspot?.config?.style?.type === 'image' &&
      hotspot?.config?.style?.image?.src,
    [hotspot],
  );
  const isStyleLottie = useMemo(
    () =>
      hotspot?.config?.style?.type === 'lottie' &&
      hotspot?.config?.style?.lottie?.json_src,
    [hotspot],
  );
  const isStyleMarker = useMemo(
    () =>
      hotspot?.config?.style?.type === 'marker' &&
      hotspot?.config?.style?.marker?.svg_src,
    [hotspot],
  );
  const isStyleCallout = useMemo(
    () =>
      hotspot?.config?.style?.type === 'callout' &&
      (hotspot?.config?.style?.callout?.src ||
        hotspot?.config?.style?.callout?.text),
    [hotspot],
  );
  const optionPreviewBox = useMemo(() => {
    switch (hotspot?.type) {
      case 'point':
        return {
          isHidePreview: !!hotspot.config?.point?.preview_box_hide,
          isHideTitleOnTop: !!hotspot.config?.point?.title_on_top_hide,
        };
      case 'image':
        return {
          isHidePreview: !!hotspot.config?.image?.preview_box_hide,
          isHideTitleOnTop: !!hotspot.config?.image?.title_on_top_hide,
        };
      case 'video':
        return {
          isHidePreview: !!hotspot.config?.video?.preview_box_hide,
          isHideTitleOnTop: !!hotspot.config?.video?.title_on_top_hide,
        };
      case 'article':
        return {
          isHidePreview: !!hotspot.config?.article?.preview_box_hide,
          isHideTitleOnTop: !!hotspot.config?.article?.title_on_top_hide,
        };
      case 'link':
        return {
          isHidePreview: !!hotspot.config?.link?.preview_box_hide,
          isHideTitleOnTop: !!hotspot.config?.link?.title_on_top_hide,
        };
      default:
        return { isHidePreview: false, isHideTitleOnTop: false };
    }
  }, [hotspot]);

  const onVisibleFlagPopover = useCallback(() => {
    flagCheckVisibleRef.current = true;
  }, []);

  const onUnvisibleFlagPopover = useCallback(() => {
    flagCheckVisibleRef.current = false;
  }, []);

  const onVisiblePopover = useCallback(() => {
    setVisiblePopover(true);
    onChangePopover(true);
  }, [onChangePopover]);

  const onUnvisiblePopover = useCallback(() => {
    onUnvisibleFlagPopover();
    setVisiblePopover(false);
    onChangePopover(false);
  }, [onChangePopover, onUnvisibleFlagPopover]);

  return (
    <HotspotWrapper
      isHidden={isHidden}
      type={
        hotspot?.config?.style?.type === 'icon' &&
        hotspot?.config?.style?.icon?.shape
          ? ('icon-shape' as const)
          : hotspot?.config?.style?.type || ('icon' as const)
      }
      size={hotspot?.config?.style?.size || 3}
      rotate={hotspot?.config?.style?.rotate || 0}
      opacity={(hotspot?.config?.style?.opacity || 100) / 100}
      onMouseOver={onVisiblePopover}
      onMouseLeave={() => {
        setTimeout(() => {
          if (!flagCheckVisibleRef.current) onUnvisiblePopover();
        }, 200);
      }}
      onClick={onUnvisiblePopover}
      onTouchEnd={onUnvisiblePopover}
    >
      <Popover
        open={
          (isHidePreviewGlobal && isHideTitleGlobal) ||
          (optionPreviewBox.isHidePreview && isHideTitleGlobal) ||
          (isHidePreviewGlobal && !hotspot.title) ||
          (optionPreviewBox.isHidePreview && !hotspot.title) ||
          hotspot.type === 'sound'
            ? false
            : visiblePopover
        }
        content={
          <Preview
            hotspot={hotspot}
            listPost={listPost}
            listScene={listScene}
            isHidePreview={optionPreviewBox.isHidePreview}
            isHideTitleGlobal={isHideTitleGlobal}
            onUnvisiblePopover={onUnvisiblePopover}
            isHidePreviewGlobal={isHidePreviewGlobal}
            onVisibleFlagPopover={onVisibleFlagPopover}
            onUnvisibleFlagPopover={onUnvisibleFlagPopover}
            onClick={() => {
              onUnvisiblePopover();
              onClick();
            }}
          />
        }
        placement={hotspot.type === 'article' ? 'right' : 'top'}
        destroyTooltipOnHide
        mouseLeaveDelay={500}
        onOpenChange={state => {
          if (!state) flagCheckVisibleRef.current = false;
        }}
        overlayClassName="popoverPreviewHotspot"
        zIndex={99}
      >
        {hotspot.title &&
          !isHideTitleOnTopGlobal &&
          !optionPreviewBox.isHideTitleOnTop && (
            <span className="--title">
              <em>{hotspot.title}</em>
            </span>
          )}
        <div
          className={`content animate__animated ${
            hotspot?.config?.style?.effect &&
            hotspot?.config?.style?.effect !== 'normal'
              ? hotspot?.config?.style?.effect
              : ''
          }`}
          onClick={onClick}
          onTouchEnd={onClick}
        >
          {!isStyleImage &&
            !isStyleShape &&
            !isStyleLottie &&
            !isStyleCallout &&
            !isStyleMarker && (
              <DefaultIcon
                typeHotspot={hotspot?.type}
                bgColorDefault={
                  hotspot.config?.style?.icon?.bg_color || bgColorDefault
                }
                iconColorDefault={iconColorDefault}
              />
            )}
          {isStyleShape && (
            <ShapeIcon
              typeHotspot={hotspot?.type}
              bgColorDefault={bgColorDefault}
              iconColorDefault={iconColorDefault}
              hotspotStyleShape={hotspot.config?.style?.icon}
            />
          )}
          {isStyleImage && <ImageIcon image={hotspot.config?.style?.image} />}
          {isStyleLottie && (
            <LottieIcon lottie={hotspot.config?.style?.lottie} />
          )}
          {isStyleMarker && (
            <MarkerIcon
              hotspot={hotspot}
              listScene={listScene}
              marker={hotspot.config?.style?.marker}
            />
          )}
          {isStyleCallout && (
            <CalloutIcon
              hotspotTitle={hotspot.title || '---'}
              callout={hotspot.config?.style?.callout}
            />
          )}
        </div>
      </Popover>
    </HotspotWrapper>
  );
};

export default memo(Hotspot);
